import { Component, OnInit, Input } from '@angular/core';
import { apiImagenesUrl } from 'src/environments/environment.prod';
@Component({
  selector: 'app-category-view',
  templateUrl: './category-view.component.html',
  styleUrls: ['./category-view.component.scss']
})

export class CategoryViewComponent implements OnInit {

  @Input() category: any;
  @Input() products: any[];
  @Input() full: boolean;
  @Input() selectCategory!: (i: string) => void;
  @Input() selectProduct: (product: any) => void;
  public nullImage = "null";
  public imgDefault: any = '../../../../../assets/img/img_Default/default1.png'
  urlImgs: string;
  public rows: RowLimit[] = [
    { start: 0, end: 4 },
    { start: 4, end: 8 },
    { start: 8, end: 12 },
    { start: 12, end: 16 },
    // { start: 16, end: 20 },
    // { start: 20, end: 24 },
    // { start: 24, end: 28 },
  ]
  constructor() { }

  ngOnInit() {
    this.urlImgs = apiImagenesUrl.url;
  }
  clickCategory(categoryId: number) {
    this.selectCategory('' + categoryId);
  }

}

interface RowLimit {
  start: number;
  end: number;
}
