import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AuthService } from 'src/app/core/services/auth.service';
import { LocalStorageService } from 'src/app/core/services/local-storage.service';
import { ToolsService } from 'src/app/core/services/tools.service';
import { apiImagenesUrl } from 'src/environments/environment.prod';

@Component({
  selector: 'app-shopping-cart',
  templateUrl: './shopping-cart.component.html',
  styleUrls: ['./shopping-cart.component.scss']
})
export class ShoppingCartComponent implements OnInit {
  public nullImage = "null";
  urlImgs: any;
  imgDefault: any = '../../../../../assets/img/img_Default/default1.png';
  constructor(
    private router: Router,
    private notificationService: ToolsService, private localStorageService: LocalStorageService, private auth: AuthService) {
  }
  user: any = {};
  ngOnInit() {
    this.user = JSON.parse(localStorage.getItem('_user'));
    this.urlImgs = apiImagenesUrl.url;
  }
  getShoppingCart(): any[] {
    return JSON.parse(this.localStorageService.getValue('_shopping_cart')) || [];
  }
  deleteFromCart(index: number) {
    let shoppingCart = JSON.parse(this.localStorageService.getValue('_shopping_cart'));
    shoppingCart = shoppingCart.filter((foo: any, productIndex: number) => productIndex !== index);
    this.localStorageService.saveValue('_shopping_cart', shoppingCart);
  }
  increaseQuantity(index: number) {
    this.operationQuantity(index, 'sum');
  }
  decreaseQuantity(index: number) {
    this.operationQuantity(index, 'subtraction');
  }
  operationQuantity(index: number, operation: string) {
    let shoppingCart = JSON.parse(this.localStorageService.getValue('_shopping_cart'));
    let product = shoppingCart[index];
    operation == 'sum' ? product.quantity++ : product.quantity--;
    if (product.quantity === 0) {
      this.deleteFromCart(index);
      return;
    }
    this.localStorageService.saveValue('_shopping_cart', shoppingCart);
  }
  getTotal() {
    let shoppingCart = this.getShoppingCart();
    return shoppingCart.reduce(
      (previousValue, current) =>
        previousValue + (current.quantity * current.price)
      , 0);
  }
  toPay() {
    if (!this.user) {
      this.notificationService.NotificationAdver(
        "Paso extra",
        "Necesitamos que te registres o inicies sesin para poder terminar tu compra",
        3000
      );
      this.router.navigate(['/login/signup']);
    } else {
      this.localStorageService.saveValue('_shopping_cart', []);
      this.notificationService.NotificationExito(
        "Ya estamos a un paso",
        `Te enviamos un correo a ${this.user.email} para confirmar tu compra`,
        2000
      );
      this.router.navigate(['/home']);
    }
  }
}
