import { Component, Input, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { LocalStorageService } from 'src/app/core/services/local-storage.service';
import { RestService } from 'src/app/core/services/rest.service';
import { apiImagenesUrl } from 'src/environments/environment.prod';

@Component({
  selector: 'app-product-view',
  templateUrl: './product-view.component.html',
  styleUrls: ['./product-view.component.scss']
})
export class ProductViewComponent implements OnInit {
  @Input() product: Product;
  @Input() featured: Boolean;
  @Input() selectProduct: (product: any) => void;
  constructor(public localStorageService: LocalStorageService,
    public restService: RestService, private router: Router) { }
  public nullImage = "null";
  imgDefault: any = '../../../../../assets/img/img_Default/default1.png';
  public urlImgs: any;
  ngOnInit() {
    this.urlImgs = apiImagenesUrl.url;
  }
  toogleLike(productId: number): void {
    let likedProductsId: number[] = JSON.parse(this.localStorageService.getValue('_liked_products_id')) || [];
    let updatedLikedProductsId: number[] = [];
    if (likedProductsId.includes(productId)) {
      updatedLikedProductsId = likedProductsId.filter(candidateProductId => candidateProductId !== productId);
      this.localStorageService.saveValue('_liked_products_id', updatedLikedProductsId);
      this.restService.put('/rest/productlike/' + productId).then(response => { console.log(response) })
    } else {
      Array.prototype.push.apply(likedProductsId, [productId]);
      this.localStorageService.saveValue('_liked_products_id', likedProductsId);
    }
  }
  productIsLiked(productId: number): boolean {
    let likedProductsId: number[] = JSON.parse(this.localStorageService.getValue('_liked_products_id')) || [];
    return likedProductsId.includes(productId);
  }
  addProductToCart(product: Product): void {
    let shoppingCart: Product[] = JSON.parse(this.localStorageService.getValue('_shopping_cart')) || [];
    let existingProductInCart: Product = shoppingCart.find(candidateProduct =>
      candidateProduct.id === product.id);
    if (existingProductInCart) {
      existingProductInCart.quantity = (existingProductInCart.quantity || 0) + 1;
      //actualice
      shoppingCart = shoppingCart.map(candidateProduct =>
        candidateProduct.id === existingProductInCart.id
          ? existingProductInCart : candidateProduct)
    } else {
      Array.prototype.push.apply(shoppingCart, [{ ...product, quantity: 1 }]);
    }
    this.localStorageService.saveValue('_shopping_cart', shoppingCart);
  }
  goToShoppingCart(): void {
    this.router.navigate(['/home/shopping-cart']);
  }
  purchaseAction(product: Product): void {
    this.addProductToCart(product);
    this.goToShoppingCart();
  }
}

interface Product {
  active: any;
  category_father_id: any;
  category_id: any;
  created_at: any;
  deleted_at: any;
  description: any;
  flag: any;
  id: any;
  image1: any;
  image10: any;
  image2: any;
  image3: any;
  image4: any;
  image5: any;
  image6: any;
  image7: any;
  image8: any;
  image9: any;
  likes: any;
  name: any;
  negotiable_price: any;
  price: any;
  reported: any;
  seen: any;
  updated_at: any;
  user_id: any;
  web_positioning: any;
  quantity?: number;
}
